<template>
  <section>
    <div v-if="loading">
      <h3 class="fa fa-spinner">Loading...</h3>
    </div>
    <div v-if="error">
      <router-link to="/">Go Back</router-link>
    </div>
    <div v-if="profileData" class="container mx-auto">
      <div class="card mb-3" style="max-width: 800px;">
        <div class="row no-gutters">
          <div class="col-md-4">
            <img :src="profileData.platformInfo.avatarUrl" class="card-img">
          </div>
          <div class="col-md-8">
            <div class="card-body">
              <span class="card-title badge badge-pill badge-dark mb-3">{{ profileData.platformInfo.platformUserHandle }}</span>
              <p class="card-text"><b>Time Played:</b> {{ profileDataStats.timePlayed.displayValue }}</p>
              <p class="card-text"><b>Kills:</b> {{ profileDataStats.kills.displayValue }}</p>
              <p class="card-text"><b>Deaths:</b> {{ profileDataStats.deaths.displayValue }}</p>
              <p class="card-text"><b>Kill/Deaths:</b> {{ profileDataStats.kd.displayValue }}</p>
              <p class="card-text"><b>Headshots:</b> {{ profileDataStats.headshots.displayValue }}</p>
              
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      loading: false,
      error: null,
      profileData: null,
      profileDataStats: null
    }
  },
  beforeCreate() {
    document.body.className = 'body-bg-no-image';
  },
  async created() {
    this.loading = true;
    try {
      const res = await axios.get(`/api/v1/profile/steam/${this.$route.params.steamid}`);
      this.profileData = res.data.data;
      this.profileDataStats = this.profileData.segments[0].stats;
      console.log(this.profileData);
      this.loading = false;
    } catch (error) {
      this.loading = false;
      this.error = error.response.data.msg;
    }
  }
}
</script>

<style scoped>
.card-title {
  font-weight: bold;
  font-size: 25px;
}
.card-img {
  padding: 10px;
}
</style>