<template>
  <section class="search">
    <h1 id="title">Track Player Stats</h1>
    <form @submit.prevent="onSubmit">
      <div class="form-group">
        <input type="text" v-model="steamID" id="steamID" placeholder="Enter your Steam ID">
      </div>
      <div class="form-group">
        <input type="submit" value="Get Stats" class="btn" id="submit-button">
      </div>
    </form>
  </section>
</template>

<script>
export default {
  name: 'Search',
  data() {
    return {
      steamID: ''
    }
  },
  methods: {
    onSubmit() {
      if(!this.steamID) {
        this.$toasted.show('Please enter a Steam ID', {
          duration: 3000,
          icon: 'exclamation-circle',
          type: 'error'
        });
      } else {
        this.$router.push(`/profile/steam/${this.steamID}`);
      }
    }
  }
}
</script>

<style>
#title {
  color: white;
}
#steamID {
  padding: 10px;
}
#submit-button:hover {
  background-color: rgb(189, 146, 8);
}
</style>